import React, { useState, useEffect } from "react";
import "./QuestionCard.css";

const QuestionCard = ({
  question,
  questionNumber,
  totalQuestions,
  chapterNumber,
  chapterName,
  onAnswer,
  onBack,
  onSkip,
  onJump,
}) => {
  const [selected, setSelected] = useState(null);
  const [textValue, setTextValue] = useState("");

  // ì§ˆë¬¸ì´ ë°”ë€” ë•Œ ì„ íƒ ì´ˆê¸°í™”
  useEffect(() => {
    setSelected(null);
    setTextValue("");
  }, [question]);

  // ì˜µì…˜ ë°°ì—´ ì•ˆì „ ì²˜ë¦¬ (ê°•í™”ëœ ë²„ì „)
  const getOptionList = () => {
    const opts = question.options;
    
    // ë””ë²„ê¹…
    console.log("ğŸ” Raw options:", opts, "Type:", typeof opts);
    
    if (!opts) return [];
    
    // ì´ë¯¸ ë°°ì—´ì¸ ê²½ìš°
    if (Array.isArray(opts)) {
      // ë°°ì—´ì˜ ì²« ìš”ì†Œê°€ íŒŒì´í”„ë¡œ êµ¬ë¶„ëœ ë¬¸ìì—´ì¸ì§€ í™•ì¸
      if (opts.length === 1 && typeof opts[0] === "string" && opts[0].includes("|")) {
        const split = opts[0].split("|").map((o) => o.trim()).filter(Boolean);
        console.log("ğŸ”§ Split from array[0]:", split);
        return split;
      }
      console.log("âœ… Already array:", opts);
      return opts;
    }
    
    // ë¬¸ìì—´ì¸ ê²½ìš°
    if (typeof opts === "string") {
      // íŒŒì´í”„(|)ë¡œ ë¶„ë¦¬
      if (opts.includes("|")) {
        const split = opts.split("|").map((o) => o.trim()).filter(Boolean);
        console.log("ğŸ”§ Split by pipe:", split);
        return split;
      }
      // ì‰¼í‘œ(,)ë¡œ ë¶„ë¦¬
      if (opts.includes(",")) {
        const split = opts.split(",").map((o) => o.trim()).filter(Boolean);
        console.log("ğŸ”§ Split by comma:", split);
        return split;
      }
      // ë‹¨ì¼ ê°’
      console.log("âš ï¸ Single value:", [opts.trim()]);
      return [opts.trim()];
    }
    
    return [];
  };

  const optionList = getOptionList();
  
  // ë””ë²„ê¹…: ìµœì¢… ì˜µì…˜ ë¦¬ìŠ¤íŠ¸
  console.log("ğŸ“‹ Final optionList:", optionList, "Length:", optionList.length);

  // ì˜µì…˜ ì„ íƒ í•¸ë“¤ëŸ¬
  const handleOptionClick = (opt, idx) => {
    setSelected(idx);
  };

  // Next ë²„íŠ¼ í´ë¦­
  const handleNext = () => {
    if (question.type === "text") {
      if (textValue.trim()) {
        onAnswer(textValue);
        setTextValue("");
      }
    } else if (selected !== null) {
      onAnswer(optionList[selected]);
      setSelected(null);
    }
  };

  // Back ë²„íŠ¼
  const handleBack = () => {
    setSelected(null);
    setTextValue("");
    if (onBack) onBack();
  };

  // Skip ë²„íŠ¼
  const handleSkip = () => {
    setSelected(null);
    setTextValue("");
    if (onSkip) onSkip();
  };

  // Jump ë²„íŠ¼
  const handleJump = () => {
    setSelected(null);
    setTextValue("");
    if (onJump) onJump();
  };

  // Next ë²„íŠ¼ í™œì„±í™” ì¡°ê±´
  const isNextEnabled =
    question.type === "text" ? textValue.trim().length > 0 : selected !== null;

  // ì˜µì…˜ì´ ì œëŒ€ë¡œ ë¶„ë¦¬ë˜ì—ˆëŠ”ì§€ í™•ì¸ (5ê°œ Likert ì²™ë„ ê¸°ëŒ€)
  const hasValidOptions = optionList.length > 1;

  return (
    <div className="question-card">
      {/* ğŸ”¹ Section Header */}
      <div className="qc-section-header">
        <div className="qc-section-left">
          <span className="qc-section-number">Section {chapterNumber}.</span>
          <span className="qc-section-name">{chapterName}</span>
        </div>
        <div className="qc-section-progress">
          {questionNumber} / {totalQuestions}
        </div>
      </div>

      {/* ğŸ”¹ Question Text */}
      <h2 className="qc-question-text">{question.text}</h2>

      {/* ğŸ”¹ TEXT TYPE */}
      {question.type === "text" && (
        <textarea
          className="qc-textarea"
          placeholder="Write your answer..."
          value={textValue}
          onChange={(e) => setTextValue(e.target.value)}
        />
      )}

      {/* ğŸ”¹ MULTI / LIKERT OPTIONS */}
      {(question.type === "multi" || question.type === "likert") && hasValidOptions && (
        <div className="qc-options-wrapper">
          {optionList.map((opt, idx) => (
            <button
              key={idx}
              className={`qc-option-btn ${selected === idx ? "selected" : ""}`}
              onClick={() => handleOptionClick(opt, idx)}
            >
              {question.type === "multi" && (
                <span className="qc-option-number">{idx + 1}</span>
              )}
              <span className="qc-option-text">{opt}</span>
            </button>
          ))}
        </div>
      )}

      {/* ğŸ”¹ ì˜µì…˜ì´ ë¶„ë¦¬ ì•ˆ ëœ ê²½ìš° - ìˆ˜ë™ Likert ë²„íŠ¼ í‘œì‹œ */}
      {(question.type === "multi" || question.type === "likert") && !hasValidOptions && (
        <div className="qc-options-wrapper">
          {["Strongly Agree", "Agree", "Neutral", "Disagree", "Strongly Disagree"].map((opt, idx) => (
            <button
              key={idx}
              className={`qc-option-btn ${selected === idx ? "selected" : ""}`}
              onClick={() => handleOptionClick(opt, idx)}
            >
              <span className="qc-option-text">{opt}</span>
            </button>
          ))}
        </div>
      )}

      {/* ğŸ”¹ Buttons Row */}
      <div className="qc-button-row">
        <button className="qc-btn back" onClick={handleBack}>
          <span className="btn-icon">â†</span>
          <span className="btn-label">Back</span>
        </button>

        <button className="qc-btn skip" onClick={handleSkip}>
          <span className="btn-icon">â­</span>
          <span className="btn-label">Skip</span>
        </button>

        <button className="qc-btn jump" onClick={handleJump}>
          <span className="btn-icon">â†’</span>
          <span className="btn-label">Jump</span>
        </button>

        <button
          className={`qc-btn next ${isNextEnabled ? "active" : ""}`}
          disabled={!isNextEnabled}
          onClick={handleNext}
        >
          <span className="btn-label">Next</span>
          <span className="btn-icon">â†’</span>
        </button>
      </div>
    </div>
  );
};

export default QuestionCard;
